* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Boston";
}
html,
body {
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
}

/* Website Scrollbar Styling */

::-webkit-scrollbar {
  width: 10px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background-color: #1300bf1f;
  border-radius: 50px;
}

::-webkit-scrollbar-thumb {
  border-radius: 50px;
  background-color: #5dd1ff;
}

/* Styling For Services Section */

.service-bottom {
  position: relative;
  height: 300vh;
}

.services-container {
  /* height: 100vh; */
  position: sticky;
  top: 0px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  overflow: hidden;
  padding-top: 150px;
}

.services {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 20px;
  padding: 0;
}

/* Contact Form Styling */

.form-banner-area {
  background: url("../../public/./contact-form-background-image.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

/* Styling Input Field */

.phone-input {
  -moz-appearance: textfield;
}

.phone-input::-webkit-outer-spin-button,
.phone-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Styling For Editor */

.editorjsdiv {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.editorjsdivdetails {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

@media (max-width: 1280px) {
  .editorjsdivdetails {
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
  }
}


.editorjsdiv h2 {
  font-size: 30px;
  line-height: 36px;
  color: #369DC5;
  font-weight: 600;
  margin-top: 10px;
}

@media (min-width: 768px) {
  .editorjsdiv h2 {
    font-size: 24px ;
    line-height: 32px ;
